<template>
  <v-card
    height="100%"
  >
    <div
      id="docentes-preuniversitarios-categoria"
    >
      <v-card-title class="breakword">
        <v-row no-gutters>
          <v-col cols="11">
            Categoría.
          </v-col>
          <v-col
            cols="1"
          >
            <v-tooltip
              bottom
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  id="downloadBtn"
                  icon
                  fab
                  small
                  v-bind="attrs"
                  v-on="on"
                  @click="downloadIndicador('docentes-preuniversitarios-categoria')"
                >
                  <v-icon>{{ mdiDownload }}</v-icon>
                </v-btn>
              </template>
              <span>Descargar indicador.</span>
            </v-tooltip>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-subtitle>Distribución de los/as docentes preuniversitarios.</v-card-subtitle>
      <v-card-text>
        <v-responsive>
          <GChart
            id="charto"
            :data="barChartData"
            :options="barChartOptions"
            type="BarChart"
          />
        </v-responsive>
        <v-simple-table
          dense
        >
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">
                  Categoría
                </th>
                <th class="text-left">
                  Cargos
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(item, index) in categoriaData"
                :key="index"
              >
                <td>{{ parseCategoria(index) }}</td>
                <td>{{ item }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-card-text>
    </div>
  </v-card>
</template>
<script>
import { mapGetters, mapState } from 'vuex'
import { mdiDownload } from '@mdi/js'
export default {
  data: () => ({
    mdiDownload
  }),
  computed: {
    ...mapState(['dark', 'colors']),
    ...mapGetters(['preuniversitarios', 'mobileSize']),
    barChartOptions () {
      return {
        chartArea: {
          left: 220,
          right: 20,
          top: 20,
          bottom: 20
        },
        // tooltip: {
        //   isHtml: true,
        //   textStyle: { color: 'white', bold: false }
        // },
        colors: this.colors,
        title: '',
        backgroundColor: { fill: 'transparent' },
        height: 350,
        legend: {
          position: 'none',
          maxLines: 3,
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        },
        bar: { groupWidth: '70%' },
        vAxis: {
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        },
        hAxis: {
          format: '0',
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        }
      }
    },
    barChartData () {
      return {
        cols: [
          { id: 'Categoria', label: 'Cargos', type: 'string' },
          { id: 'Categoria', label: 'Cargos', type: 'number' }
        ],
        rows: [
          {
            c: [
              { v: 0, f: 'Director' },
              { v: this.categoriaData.dir, f: this.categoriaData.dir }
            ]
          },
          {
            c: [
              { v: 0, f: 'Vicedirector' },
              { v: this.categoriaData.vic, f: this.categoriaData.vic }
            ]
          },
          {
            c: [
              { v: 0, f: 'Regente' },
              { v: this.categoriaData.reg, f: this.categoriaData.reg }
            ]
          },
          {
            c: [
              { v: 0, f: 'Profesor nivel medio' },
              { v: this.categoriaData.pnm, f: this.categoriaData.pnm }
            ]
          },
          {
            c: [
              { v: 0, f: 'Bibliotecario' },
              { v: this.categoriaData.bib, f: this.categoriaData.bib }
            ]
          },
          {
            c: [
              { v: 0, f: 'Profesor equipo orientación' },
              { v: this.categoriaData.peo, f: this.categoriaData.peo }
            ]
          },
          {
            c: [
              { v: 0, f: 'Jefe trabajos prácticos NM' },
              { v: this.categoriaData.jtp, f: this.categoriaData.jtp }
            ]
          },
          {
            c: [
              { v: 0, f: 'Jefe/coo./dir. departamento' },
              { v: this.categoriaData.jcd, f: this.categoriaData.jcd }
            ]
          },
          {
            c: [
              { v: 0, f: 'Jefe gral. taller ens. práctica' },
              { v: this.categoriaData.jte, f: this.categoriaData.jte }
            ]
          },
          {
            c: [
              { v: 0, f: 'Jefe enseñanza práctica' },
              { v: this.categoriaData.jep, f: this.categoriaData.jep }
            ]
          },
          {
            c: [
              { v: 0, f: 'Maestros enseñanza práctica' },
              { v: this.categoriaData.mep, f: this.categoriaData.mep }
            ]
          }
        ]
      }
    },
    categoriaData () {
      // "Director Col Secund"
      // "Vicedirector"
      // "Regente"
      // "Prof nivel medio"
      // "Bibliotecario"
      // "PROF EQUIPO ORIENTAC" || "Prof. Eq. Orientacio"
      // "Jef Trab Pract NM"
      // "Jefe/Coord/Dir dept"
      // "Jefe Gral Taller EP"
      //  "Jef Gral Ens Pract"
      //  "Maestros Ens Practic"
      return this.preuniversitarios
        .reduce(
          (acc, cur) => {
            const director = cur.descripcion_categoria === 'Director Col Secund'
            const vice = cur.descripcion_categoria === 'Vicedirector'
            const regente = cur.descripcion_categoria === 'Regente'
            const profnivmed = cur.descripcion_categoria === 'Prof nivel medio'
            const bibliotecario = cur.descripcion_categoria === 'Bibliotecario'
            const profeqorient = cur.descripcion_categoria === 'PROF EQUIPO ORIENTAC' || cur.descripcion_categoria === 'Prof. Eq. Orientacio'
            const jeftepra = cur.descripcion_categoria === 'Jef Trab Pract NM'
            const jcordir = cur.descripcion_categoria === 'Jefe/Coord/Dir dept'
            const jeftallep = cur.descripcion_categoria === 'Jefe Gral Taller EP'
            const jefnenspra = cur.descripcion_categoria === 'Jef Gral Ens Pract'
            const maenspra = cur.descripcion_categoria === 'Maestros Ens Practic'
            return director
              ? { ...acc, dir: acc.dir + 1 }
              : vice
                ? { ...acc, vic: acc.vic + 1 }
                : regente
                  ? { ...acc, reg: acc.reg + 1 }
                  : profnivmed
                    ? { ...acc, pnm: acc.pnm + 1 }
                    : bibliotecario
                      ? { ...acc, bib: acc.bib + 1 }
                      : profeqorient
                        ? { ...acc, peo: acc.peo + 1 }
                        : jeftepra
                          ? { ...acc, jtp: acc.jtp + 1 }
                          : jcordir
                            ? { ...acc, jcd: acc.jcd + 1 }
                            : jeftallep
                              ? { ...acc, jte: acc.jte + 1 }
                              : jefnenspra
                                ? { ...acc, jep: acc.jep + 1 }
                                : maenspra
                                  ? { ...acc, mep: acc.mep + 1 }
                                  : acc
          },
          {
            dir: 0,
            vic: 0,
            reg: 0,
            pnm: 0,
            bib: 0,
            peo: 0,
            jtp: 0,
            jcd: 0,
            jte: 0,
            jep: 0,
            mep: 0
          }
        )
    }
  },
  methods: {
    parseCategoria (v) {
      return v === 'dir'
        ? 'Director'
        : v === 'vic'
          ? 'Vicedirector'
          : v === 'reg'
            ? 'Regente'
            : v === 'pnm'
              ? 'Profesor nivel medio'
              : v === 'bib'
                ? 'Bibliotecario'
                : v === 'peo'
                  ? 'Profesor equipo orientación'
                  : v === 'jtp'
                    ? 'Jefe trabajos prácticos NM'
                    : v === 'jcd'
                      ? 'Jefe/coordinador/director departamento'
                      : v === 'jte'
                        ? 'Jefe gral. taller enseñanza práctica'
                        : v === 'jep'
                          ? 'Jefe enseñanza práctica'
                          : v === 'mep'
                            ? 'Maestros enseñanza práctica'
                            : v
    }
  }
}
</script>
